.isDone {
  opacity: .5;
}

.app {
  .headerPosition {
    position: relative;
  }
  .linearProgress {
    position: absolute;
    z-index: 10;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

.previewCircularProgress {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
